.Form {
    padding: 20px;
}

.Header {
    text-align: center;
    font-size: 30px;
    font-weight: bolder;
}

.ErrorMessage {
    font-size: 1rem !important;
    font-weight: bolder;
}

.Button, .Input {
    border: 0;
    outline: 0;
    font-size: 16px;
    padding: 10px;
}

.Input {
    border: 0;
    outline: 0;
    font-size: 16px;
    background-color: #EBECF0;
    margin-right: 32px;
    box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px #FFF;
    width: 100%;
    padding: 16px;
    box-sizing: border-box;
    transition: all 0.2s ease-in-out;
    appearance: none;
    -webkit-appearance: none;
}

.Input:focus {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #FFF;
}

body, header, .Input, .Button {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
}

.Button {
    box-shadow: 3px 3px 10px #661f1f;
    cursor: pointer;
    letter-spacing: 0.2rem;
    color: #FFF;
}

.Button:hover {
    box-shadow: 2px 2px 5px #000000;
}

.Button:active {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #000000;
}

.Submit, .Update {
    background-color: #009b72;
}

.Reset, .Cancel {
    background-color: #ffffff;
    color: #000000;
}